<template>
  <div class="project-manage-table" ref="projectManageTableRef" :class="$root.themeHomeChange === '1' ?'table_light':''">
    <el-form
      :model="form"
      :rules="rules"
      ref="form"
      label-width="84px"
      label-suffix="："
    >
      <el-row>
        <el-col :span="10">
          <!--类型-按钮组-->
          <div class="manageBtn" background-color="#00AAFF">
            <bda-button @click="addDialogClick">新增</bda-button>
            <bda-button @click="handleDel">批量删除</bda-button>
            <bda-button @click="importDocClick">导入</bda-button>
            <bda-button @click="exportExcel">导出</bda-button>
            <bda-button @click="downloadTemplate">下载模板</bda-button>
            <bda-button
                v-if="false"
              :key="$route.query.isRelation"
              @click="relationList"
            >关联勾选内容</bda-button>
          </div>
        </el-col>
        <el-col :span="4">
          <bda-form-item label="知识库类型" prop="section">
            <bda-select
                v-model="form.zskValue"
                size="small"
                filterable
                clearable
                placeholder="请选择知识库类型"
                style="width: 100%"
            >
              <bda-option
                  v-for="item in zskArray"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code"
              >
              </bda-option>
            </bda-select>
          </bda-form-item>
        </el-col>
        <el-col :span="4">
          <bda-form-item label="所属部门" prop="section">
            <bda-select
              v-model="form.section"
              size="small"
              filterable
              clearable
              placeholder="请选择"
              style="width: 100%"
            >
              <bda-option
                v-for="item in sectionList"
                :key="item.code"
                :label="item.name"
                :value="item.code"
              >
              </bda-option>
            </bda-select>
          </bda-form-item>
        </el-col>
        <el-col :span="4">
          <bda-search-input
            v-model.trim="form.keyValue"
            placeholder="请输入关键字"
            style="margin-left: 20px"
          ></bda-search-input>
        </el-col>
        <el-col :span="1">
          .
        </el-col>
        <el-col :span="1">
          <bda-button @click="queryDataPage">搜索</bda-button>
        </el-col>
      </el-row>
    </el-form>

    <bda-scroll-table
      background
      v-loading="tableLoading"
      element-loading-text="拼命加载中"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)"
      :data="filterData"
      ref="table"
      popper-append-to-body
      border
      :blInitLoad="false"
      tooltip-effect="light"
      :style="{ height: tableHeight }"
      @select="handleSelectionChange"
    >
      <el-table-column style="background-color: #041338" type="selection" width="48"></el-table-column>
      <el-table-column prop="knowledgeName" label="知识库名称" min-width="300" show-overflow-tooltip></el-table-column>
      <el-table-column prop="version" label="最新版本号" min-width="100" show-overflow-tooltip></el-table-column>
      <el-table-column prop="knowledgeType" label="知识库类别" min-width="120" show-overflow-tooltip></el-table-column>
      <el-table-column prop="systemBusinessType" label="制度业务类别" min-width="200"></el-table-column>
      <el-table-column prop="systemFunctionOrientation" label="制度功能定位" min-width="200"></el-table-column>
      <el-table-column prop="bbjg" label="颁布机构" min-width="200"></el-table-column>
      <el-table-column prop="bbsj" label="颁布时间" min-width="100" show-overflow-tooltip></el-table-column>
      <el-table-column
        prop="updateStatus"
        label="修订状态"
        min-width="80"
        show-overflow-tooltip>
        <template slot-scope="scope">{{scope.row.updateStatus===0?'待修订':'已修订'}}</template>
      </el-table-column>
      <el-table-column prop="department" label="所属部门" min-width="120" show-overflow-tooltip></el-table-column>
      <el-table-column prop="duty" label="岗位名称" min-width="120" show-overflow-tooltip></el-table-column>
      <el-table-column prop="creator" label="创建者" min-width="100" show-overflow-tooltip></el-table-column>
      <el-table-column prop="createTime" label="创建时间" min-width="100" show-overflow-tooltip></el-table-column>
      <el-table-column prop="updateTime" label="更新时间" min-width="100" show-overflow-tooltip></el-table-column>
      <el-table-column prop="updateName" label="更新人" min-width="100" show-overflow-tooltip></el-table-column>
      <el-table-column
        prop="action"
        label="操作"
        min-width="180"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <el-button
            type="text"
            class="DetailBtn"
            @click="actionClick(scope.row)">文件及版本管理</el-button>
        </template>
      </el-table-column>
    </bda-scroll-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pageInfoConfig.page"
      :page-sizes="[10, 20, 30, 40, 50]"
      :page-size="pageInfoConfig.rows"
      background
      layout="prev, pager, next, sizes, jumper, total"
      :total="pageInfoConfig.total"
    >
    </el-pagination>

    <!-- 弹窗 -->
    <add-doc-dialog ref="addDocRef" @addData="addData" v-if="addView"></add-doc-dialog>
    <import-doc-dialog ref="importDocRef" v-if="showVs"></import-doc-dialog>
    <file-upload
        :url="'/knowledge/excelImport'"
        ref="fileUpload"
        :fileName="'excelFile'"
        v-if="showFileUp"
        @upLoadSuccess="fileUpSuccess"
    ></file-upload>
  </div>
</template>
<script>
import addDocDialog from "../../../../companyCenter/component/manage/dialog/addDoc.vue";
import importDocDialog from "../../../../companyCenter/component/manage/dialog/importDoc.vue";
import Interface from "../../../../../base/mixin/enterprise-manage-center";
import {API_PATH} from "@/config"
import axios from "axios";
import fileUpload from "../../../../../components/table/fileUpload";
// import qs from 'qs'
export default {
  components: { addDocDialog, importDocDialog,fileUpload },
  name: "projectManageTable",
  mixins: [Interface],
  data() {
    return {
      form: {
        section: "", //所属部门
        keyValue: "", //关键字
        zskValue: "", //知识库
      },
      zskArray: "", //知识库
      rules: {},
      sectionList: [], //所属部门列表

      /*表格数据 */
      tableData:[],
      filterData: [],

      pageInfoConfig:{
        page: 1,
        rows: 10,
        total:0
      }, //表格-分页初始配置

      tableHeight: "",
      selectIds: [], //已选数据id
      addView:false,
      tableLoading:false,//表格加载
      showVs:false,//版本组件加载

      showFileUp:false,//导入
    };
  },
  watch: {
  },
  computed: {},
  created() {
  },
  mounted() {
    this.getDepartment()//部门
    this.getManageTypeArray()//知识库类型
    this.getDocumentQuery()
  },
  methods: {
    dataChange() {
    },
    //获取知识库类型
    getManageTypeArray() {
      this.manageTypeArray({}).then((res) => {
        if (res) {
          this.zskArray = res;
        }
      }).finally(() => {
      })
    },
    //所属部门
    getDepartment(){
      this.department({}).then((res)=>{
        this.sectionList = res||[]
      })
    },

    //查询重置分页
    queryDataPage(){
      this.pageInfoConfig.page = 1
      this.pageInfoConfig.rows = 10
      this.getDocumentQuery()
    },
    //查询获取数据
    getDocumentQuery(){
      this.tableLoading = true
      this.documentQuery({
        departmentId:this.form.section,
        knowledgeType:this.form.zskValue,
        keyword:this.form.keyValue,
        size:this.pageInfoConfig.rows,
        current:this.pageInfoConfig.page,
      }).then((res)=>{
        this.pageInfoConfig.total = res.records
        this.filterData = res.rows
      }).finally(()=>{
        this.tableLoading = false
      })
    },
    // 新增
    addDialogClick() {
      this.addView = true
      this.$nextTick(()=>{
        this.$refs.addDocRef.openDialog();
        this.$refs.addDocRef.initData();
      })

    },

    // 新增数据
    addData() {
      this.addView = false
      this.getDocumentQuery()
    },
    //批量删除
    handleDel(){
      if (this.selectIds.length<1){
        this.$message.warning('请选择需要删除的数据')
        return
      }
      let params = new URLSearchParams()
      for (let i=0;i<this.selectIds.length;i++){
        params.append('knowledgeIds',this.selectIds[i])
      }
      // qs.stringify(params, { arrayFormat: 'repeat' })
      this.knowledgeDel(params).then(()=>{
          this.getDocumentQuery()
      }).finally(()=>{
      })
    },
    // 导入
    importDocClick() {
      this.showFileUp = true
      this.$nextTick(()=>{
        this.$refs.fileUpload.dialogVisible = true
      })
    },
    //导入成功钩子  参数res
    fileUpSuccess(){
      this.getDocumentQuery()
    },
    //下载模板
    downloadTemplate(){
      window.location.href = `${API_PATH}/dictionary/template?exportMark=knowledge`
    },
    //操作
    actionClick(row) {
      this.showVs = true
      this.$nextTick(()=>{
        this.$refs.importDocRef.openDialog(row);
      })
    },

    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.table.toggleRowSelection(row);
        });
      } else {
        this.$refs.table.clearSelection();
      }
    },
    
    // 传递关联列表
    relationList() {

    },

    // 弹窗 被检单位下拉选择数据处理
    handleSelectionChange(val) {
      this.selectIds = val.map((v)=>{
        return v.knowledgeId
      });
    },

    // 分页器
    handleSizeChange(val) {
      this.pageInfoConfig.rows = val
      this.getDocumentQuery()
      window.console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.pageInfoConfig.page = val
      this.getDocumentQuery()
      window.console.log(`当前页: ${val}`);
    },

    //导出excel
    exportExcel() {
      let path = '/knowledge/excelExport'
      let data = {
        departmentId:this.form.section,
        knowledgeType:this.form.zskValue,
        keyword:this.form.keyValue,
        mark:'制度',
      }
      axios({
        method: "POST",
        url: API_PATH + path,
        data:data,
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        },
        responseType: 'blob'
      }).then(res => {
        const content = res.data
        const blob = new Blob([content])
        const fileName = '导出信息.xls'
        if ('download' in document.createElement('a')) { // 非IE下载
          const elink = document.createElement('a')
          elink.download = fileName
          elink.style.display = 'none'
          elink.href = URL.createObjectURL(blob)
          document.body.appendChild(elink)
          elink.click()
          URL.revokeObjectURL(elink.href) // 释放URL 对象
          document.body.removeChild(elink)
        } else { // IE10+下载
          navigator.msSaveBlob(blob, fileName)
        }
      }).finally(()=>{
      }).catch(()=>{
      })
    },
  },
};
</script>
<style lang="scss" scoped>
.project-manage-table {
  height: 100%;
  width: 100%;
  ::v-deep .bda-main .bda-table{
    overflow-y: scroll !important;
    .el-table{
      top: 0!important;
    }
    .el-table:before{
      width: 0;
    }
    .el-table--border:after{
      width: 0;
    }
  }
  /deep/ {
    .button-container {
      margin-bottom: 10px;
    }

    /*修改表格单元格高度*/
    .bda-pagination-table {
      .bda-pagination-table-page {
        border: 0 !important;
        height: 50px !important;
      }
      .bda-pagination-table-main {
        height: calc(100% - 40px);
      }
    }

    /*checkbox样式*/

    .el-checkbox__input.is-disabled {
      width: 100%;
      padding: 0 auto;
    }

    .el-checkbox__input.is-disabled .el-checkbox__inner,
    .el-checkbox__input .el-checkbox__inner {
      background-color: #041338 !important;
      width: 16px;
      height: 16px;
      border: 1px solid #4389f9;
      border-radius: 2px;
    }

    /*解决调整单元格高度后，fixed错位*/
    .el-table__fixed-body-wrapper {
      top: 37px !important;
    }

    /*解决表格滚动高度固定导致的问题*/
    .el-table__body-wrapper.is-scrolling-left {
      height: calc(100% - 48px) !important;
    }
    .el-table__body-wrapper.is-scrolling-right {
      height: calc(100% - 48px) !important;
    }
    .el-table__body-wrapper.is-scrolling-middle {
      height: calc(100% - 48px) !important;
    }
    .el-table__fixed-body-wrapper {
      height: calc(100% - 34px) !important;
    }

    /*解决调整列宽度时，出现列内容未完整显示的问题*/
    .el-table .cell.el-tooltip {
      width: 100% !important;
    }

    .el-table__header-wrapper {
      background-color: #22519c;
    }

    .el-table__body-wrapper {
      background-color: #071a47;
    }

    .el-table__header th {
      background-color: #22519c !important;
      height: 48px;
      border-color: #000000;

      .cell {
        color: #fff !important;
        text-align: center;
      }
    }

    .bda-table-main {
      background-color: #071a47;
    }

    .bda-main {
      height: calc(100% - 104px);
      border: 1px solid #000000;
      .el-table__row {
        &:hover > td {
          background-color: #284385 !important;
        }
        > td {
          height: 48px;
          border-color: #000000;

          .cell {
            color: #fff;
            text-align: center;

            .el-button {
              border-width: 0px !important;
              background-color: transparent;
              color: #66b9ff;
            }
          }
        }
      }
      .el-table__row:nth-of-type(odd) {
        background-color: #223a75;
      }
      .el-table__row:nth-of-type(even) {
        background-color: #1a3066;
      }

      // 修改 待修定数据 行数样式
      .revision-row {
        background-color: #b8c228 !important;
      }
    }

    // 修改分页组件样式
    .el-pagination {
      margin-top: 20px;
      text-align: center;
      color: #fff;
      .btn-next,
      .btn-prev,
      .el-pager li,
      .el-select .el-input .el-input__inner,
      .el-pagination__editor.el-input .el-input__inner {
        background-color: #041338;
        color: #d3d3d3;
        border: 1px solid #2a5cb0;
        border-radius: 3px;
      }
      .el-pager li:not(.disabled).active {
        background-color: #4389f9;
      }
      .el-pagination__jump,
      .el-pagination__total {
        color: #fff;
        margin-left: 10px;
      }
    }

    // 修改input框样式
    .el-input__inner {
      background: #041338;
      color: #4298f3;
      border: 1px solid #4389f9;
    }
    &.el-input__inner::placeholder {
      color: #4298f3;
    }
    .el-select__caret {
      color: #4298f3;
    }

    .el-form-item__label {
      color: #fff;
    }
  }
}
.table_light{
  /deep/ {
    .button-container {
      margin-bottom: 10px;
    }
    .el-checkbox__inner:after{
      border-color: #333;
    }
    /*修改表格单元格高度*/
    .bda-pagination-table {
      .bda-pagination-table-page {
        border: 0 !important;
        height: 50px !important;
      }
      .bda-pagination-table-main {
        height: calc(100% - 40px);
      }
    }
    .el-form-item__label {
      color: #7f7f7f;
    }
    /*checkbox样式*/

    .el-checkbox__input.is-disabled {
      width: 100%;
      padding: 0 auto;
    }

    .el-checkbox__input.is-disabled .el-checkbox__inner,
