            }
          }
        }
        > td:nth-last-child(3) {
          .cell {
            /*color: red;*/
            text-align: center;
          }
        }
      }
      .el-table__row:nth-of-type(odd) {
        background-color: #fff;
      }
      .el-table__row:nth-of-type(even) {
        background-color: #fff;
      }
    }

    // 修改分页组件样式
    .el-pagination {
      margin-top: 20px;
      text-align: center;
      color: #fff;
      .btn-next,
      .btn-prev,
      .el-pager li,
      .el-select .el-input .el-input__inner,
      .el-pagination__editor.el-input .el-input__inner {
        background: rgba(67,137,249,0.06);
        color: #333333;
        border: 0px solid #2a5cb0;
        border-radius: 4px;
      }
      .el-pager li:not(.disabled).active {
        background-color: #4389f9;
      }
      .el-pagination__jump,
      .el-pagination__total {
        color: #545454;
        margin-left: 10px;
      }
    }
    // 修改input框样式
    .el-input__inner {
      background: #ffffff;
      color: #333333;
      border: 1px solid #4389f9;
    }
    &.el-input__inner::placeholder {
      color: #4298f3;
    }
    .el-select__caret {
      color: #4298f3;
    }

    .el-form-item__label {
      color: #333333;
    }
    .el-button{
      background: #4389f9;
    }
  }
}
</style>